

import Checklist from 'vux/src/components/checklist/index.vue'

export default {
name: 'Checklist2',
data(){
return {}
},
created(){
let options = []
let selected = []
let value = this.attrs.value
for (let i = 0; i < value.length; i++) {
if (value[i].selected) {
selected.push(value[i].text)
}
options.push(value[i].text)
}
this.options = options
this.value = selected
},
props: {
attrs: {
type: Object,
default() {
return {}
}
}
},
components: {
Checklist
}
}




